import * as React from "react"
import { Link } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"

const PoolsPage = ({ data }) => {
  const allPools = data.allMarkdownRemark.nodes
  const getImageFromGatsby = image => getImage(image)
  return (
    <Layout>
      <Seo title="Pools" />
      {allPools &&
        allPools.map(pool => (
          <section className="page-section">
            <div className="container">
              <div className="product-item">
                <div className="product-item-title d-flex">
                  <div className="bg-faded p-5 d-flex ms-auto rounded">
                    <h2 className="section-heading mb-0">
                      <span className="section-heading-upper">
                        {pool.frontmatter.subtitle}
                      </span>
                      <span className="section-heading-lower">
                        {pool.frontmatter.title}
                      </span>
                      <div className="intro-button mb-0 mt-2 text-center">
                      <Link className="btn btn-primary btn-xl" to={"/catalog/" + pool.frontmatter.url} >
                        More info
                      </Link>
                    </div>
                    </h2>
                  </div>
                </div>
                <GatsbyImage
                  className="product-item-img mx-auto d-flex rounded img-fluid mb-3 mb-lg-0 custom-box-shadow"
                  image={getImageFromGatsby(pool.frontmatter.thumbnailImage)}
                  quality={95}
                  width={500}
                  placeholder="blurred"
                  formats={["auto", "webp", "avif"]}
                  alt="sun"
                />
              </div>
            </div>
          </section>
        ))}
    </Layout>
  )
}

export default PoolsPage

export const allPoolsQuery = graphql`
  query AllProducts {
    allMarkdownRemark(
      filter: { frontmatter: { type: { eq: "poolCatalog" } } }
    ) {
      nodes {
        frontmatter {
          title
          description
          subtitle
          url
          thumbnailImage {
            childImageSharp {
              gatsbyImageData
            }
          }
        }
      }
    }
  }
`
